import './App.css';
import { useState, useEffect } from 'react';
import ChatRoom from './components/ChatRoom';
import Message from './uitils/Message';

// –ü–æ–¥–≥—Ä—É–∂–∞–µ–º –∏—Å—Ç–æ—Ä–∏—é –∏–∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Ö—Ä–∞–Ω–∏–ª–∏—â–∞
const getLocalMessages = () => {
  let messages = localStorage.getItem('messages');
  if (messages) {
    return JSON.parse(localStorage.getItem('messages'))
  } else {
    return []
  }
}

const getLocalCuid = () => {
  let cuid = localStorage.getItem('cuid');
  if (cuid) {
    return JSON.parse(localStorage.getItem('cuid'))
  } else {
    return ''
  }
}

function App() {
  const [messages, setMessages] = useState(getLocalMessages());
  const [cuid, setCuid] = useState(getLocalCuid());
  const [lastBotMsg, setLastBotMsg] = useState('');
  const [lastUserMsg, setLastUserMsg] = useState('');
  // —ç—Ç–æ –∑–Ω–∞—á–µ–Ω–∏–µ –Ω—É–∂–Ω–æ –Ω–∞–º –¥–ª—è –æ—Ç–≤–µ—Ç–∞ –∫–∞–∂–¥—ã–π —Ä–∞–∑, –∫–æ–≥–¥–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –≤–≤–æ–¥–∏—Ç –ø–æ–≤—Ç–æ—Ä–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ. –ò—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –≤ –∫–∞—á–µ—Å—Ç–≤–µ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –≤ useEffect.
  const countUserMessages = messages.filter(msg => msg.type === 'sent').length;

  // –¥–∞–Ω–Ω—ã–µ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∑–∞–ø—Ä–æ—Å–∞
  const uuid = '772c9859-4dd3-4a0d-b87d-d76b9f43cfa4';
  const euid = '00b2fcbe-f27f-437b-a0d5-91072d840ed3';
  const url = 'https://biz.nanosemantics.ru/api/bat/nkd/json/Chat.';
  // —Ç—Ä–∏ —Ç–∏–ø–∞ –º–µ—Ç–æ–¥–æ–≤ 
  const urlType = {
    init: 'init',
    event: 'event',
    request: 'request'
  }
  // –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –º–µ—Ç–æ–¥–∞ –∫–æ–Ω—Å—Ç—Ä—É–∏—Ä—É–µ–º —Å—Å—ã–ª–∫—É –∏ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã POST-–∑–∞–ø—Ä–æ—Å–∞: –º–µ–Ω—è–µ—Ç—Å—è body
  const buildFetchInput = (inputType) => {
    let fetchUrl = '';
    let reqBody = {};

    if (inputType === urlType.init) {
      fetchUrl = url + urlType.init;
      reqBody = { uuid }
    } else if (inputType === urlType.event) {
      fetchUrl = url + urlType.event;
      reqBody = {
        cuid,
        euid
      };
    } else if (inputType === urlType.request) {
      fetchUrl = url + urlType.request;
      reqBody = {
        cuid,
        text: lastUserMsg
      };
    }

    const params = {
      method: 'POST',
      headers: {
        'Content-Type': 'text/plain'
      },
      body: JSON.stringify(reqBody)
    }
    return { fetchUrl, params }
  }

  // –ø–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –æ—Ç API –∏ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –∏—Ö —Ç–∏–ø–∞ –æ–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç—ã
  const fetchData = async (inputType) => {
    try {
      const { fetchUrl, params } = buildFetchInput(inputType);
      const response = await fetch(fetchUrl, params);
      const data = await response.json();
      // –æ–±–Ω–æ–≤–ª—è–µ–º –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä —á–∞—Ç–∞ –∫–æ–≥–¥–∞ –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è
      if (inputType === urlType.init) {
        setCuid(data.result.cuid)
      }
      // –∫–æ–≥–¥–∞ —Å–æ–±—ã—Ç–∏–µ READY (–ø—Ä–∏–≤–µ—Ç—Å—Ç–≤–µ–Ω–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ) –∏–ª–∏ –º–µ—Ç–æ–¥ REQUEST (–æ—Ç–≤–µ—Ç –Ω–∞ –∑–∞–ø—Ä–æ—Å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è) - –æ–±–Ω–æ–≤–ª—è–µ–º –ø–æ—Å–ª–µ–¥–Ω–µ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ
      else {
        // –ø—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å–ª–∏ —Å–µ—Ä–≤–µ—Ä –≤–µ—Ä–Ω—É–ª –Ω–æ–≤—ã–π cuid - –æ–±–Ω–æ–≤–ª—è–µ–º –µ–≥–æ –≤–µ–∑–¥–µ. –ï—Å–ª–∏ —Ö–æ—Ç–∏–º, —á—Ç–æ–±—ã –ø—Ä–∏ —ç—Ç–æ–º –æ—á–∏—â–∞–ª—Å—è —á–∞—Ç –∏ –∏—Å—Ç–æ—Ä–∏—è - –Ω—É–∂–Ω–æ –≤—ã–∑–≤–∞—Ç—å resetChat (–Ω–µ —É–≤–µ—Ä–µ–Ω, —á—Ç–æ —Ç–∞–∫ –Ω—É–∂–Ω–æ, –æ—Å—Ç–∞–≤–ª—è—é —Ç–æ–ª—å–∫–æ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ cuid)
        urlType.request && data.result.cuid && data.result.cuid !== cuid && setCuid(data.result.cuid);
        const textValue = data.result.text.value;
        textValue && setLastBotMsg(textValue);
        // –æ—Ç–ø—Ä–∞–≤–ª—è–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ –æ—Ç –±–æ—Ç–∞ –∫–∞–∂–¥—ã–π —Ä–∞–∑, –∫–æ–≥–¥–∞ –æ–Ω–æ —è–≤–ª—è–µ—Ç—Å—è –ø—Ä–∏–≤–µ—Ç—Å—Ç–≤–µ–Ω–Ω—ã–º –∏–ª–∏ –∫–æ–≥–¥–∞ –æ–Ω–æ —Ö—Ä–∞–Ω–∏—Ç—Å—è –≤ —Å—Ç–µ–π—Ç–µ. –≠—Ç–æ –Ω—É–∂–Ω–æ, —á—Ç–æ–±—ã –∏–∑–±–µ–∂–∞—Ç—å –¥—É–ø–ª–∏–∫–∞—Ç–æ–≤ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã.
        (lastBotMsg || textValue === '–ó–¥—Ä–∞–≤—Å—Ç–≤—É–π—Ç–µ.') && addBotMsg(textValue);
      }
    } catch (error) {
      throw error;
    }
  };
  // –¥–æ–±–∞–≤–ª—è–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ –æ—Ç –±–æ—Ç–∞ –≤ –Ω–∞—à —Å–ø–∏—Å–æ–∫ —Å–æ–æ–±—â–µ–Ω–∏–π
  const addBotMsg = (msg) => {
    const newMsg = new Message(msg, 'received');
    setMessages([...messages, newMsg]);
  }
  // –ø–µ—Ä–µ–∑–∞–ø—É—Å–∫ —Ä–∞–∑–≥–æ–≤–æ—Ä–∞ –≤ –Ω–æ–≤–æ–π —Å–µ—Å—Å–∏–∏ —Å —É–¥–∞–ª–µ–Ω–∏–µ–º –∏—Å—Ç–æ—Ä–∏–∏ —Ç–µ–∫—É—â–µ–≥–æ –¥–∏–∞–ª–æ–≥–∞
  const resetChat = () => {
    localStorage.clear();
    setMessages([]);
    setCuid('');
    setLastBotMsg('');
    setLastUserMsg('');
    fetchData(urlType.init)
  };
  // –ø–æ–ª—É—á–∞–µ–º cuid –ø—Ä–∏ –ø–µ—Ä–≤–æ–º —Ä–µ–Ω–¥–µ—Ä–µ (–≤ —Å–ª—É—á–∞–µ, –µ—Å–ª–∏ —É –Ω–∞—Å –Ω–µ—Ç cuid –≤ –ª–æ–∫–∞–ª—å–Ω–æ–º —Ö—Ä–∞–Ω–∏–ª–∏—â–µ)
  useEffect(() => {
    !cuid && fetchData(urlType.init)
    // eslint-disable-next-line
  }, []);
  // –ø–æ–ª—É—á–∞–µ–º –ø—Ä–∏–≤–µ—Ç—Å—Ç–≤–µ–Ω–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ
  useEffect(() => {
    cuid && fetchData(urlType.event);
    // eslint-disable-next-line
  }, [cuid]);

  // —Å–æ—Ö—Ä–∞–Ω—è–µ–º –ø–æ—Å–ª–µ–¥–Ω–µ–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∑–∞–ø—Ä–æ—Å–∞ –Ω–∞ —Ä–µ–∞–∫—Ü–∏—é –±–æ—Ç–∞
  // –∫–∞–∂–¥—ã–π —Ä–∞–∑, –∫–æ–≥–¥–∞ –ø–æ—è–≤–ª—è–µ—Ç—Å—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ. –ú—ã –∏—Å–ø–æ–ª—å–∑—É–µ–º countUserMessages –≤–º–µ—Å—Ç–æ lastUserMsg, —á—Ç–æ–±—ã –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞—Ç—å –ø–æ–≤—Ç–æ—Ä–Ω—ã–µ —Å–æ–æ–±—â–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è (3—Ä–∞–∑–∞ –≤–≤–µ–ª lte = 3 —Ä–∞–∑–∞ –ø–æ–ª—É—á–∏–ª –æ—Ç–≤–µ—Ç—ã)
  useEffect(() => {
    fetchData(urlType.request);
    // eslint-disable-next-line
  }, [countUserMessages]);

  useEffect(() => {
    localStorage.setItem('messages', JSON.stringify(messages))
  }, [messages])

  useEffect(() => {
    localStorage.setItem('cuid', JSON.stringify(cuid))
  }, [cuid])

  return (
    <div className="App">
      <header>
        <h1>ü§ñüí¨</h1>
        <button onClick={resetChat}>–û—á–∏—Å—Ç–∏—Ç—å</button>
      </header>
      <section className='chatRoom'>
        <ChatRoom messages={messages} setMessages={setMessages} setLastUserMsg={setLastUserMsg} />
      </section>

    </div>
  );
}

export default App;
