import React from 'react'
import { useState, useEffect } from 'react';
import Message from '../uitils/Message';
import { FaPaperPlane } from "react-icons/fa";


const InputBar = ({ messages, setMessages, feedEnd, setLastUserMsg }) => {
    const [inputValue, setInputValue] = useState('');
    // –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –≤–≤–æ–¥–∞ –≤ –ø–æ–ª–µ —Å–æ–æ–±—â–µ–Ω–∏—è –≤ inputValue
    const changeEventHandler = e => {
        setInputValue(e.target.value);
    }

    // –ø–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Å–æ–∑–¥–∞–µ–º –Ω–æ–≤–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ, –¥–æ–±–∞–≤–ª—è–µ–º –≤ messages, –æ—á–∏—â–∞–µ–º –ø–æ–ª–µ –≤–≤–æ–¥–∞, —Å–∫—Ä–æ–ª–ª–∏–º –≤ –∫–æ–Ω–µ—Ü
    const sendMessageHandler = e => {
        e.preventDefault();
        const newMsg = new Message(inputValue, 'sent');
        setMessages([...messages, newMsg]);
        setLastUserMsg(newMsg.text);
        setInputValue('');
    }

    useEffect(() => {
        feedEnd.current.scrollIntoView({ behavior: 'smooth' });
    }, [messages, feedEnd])

    return (

        <form onSubmit={sendMessageHandler}>
            <input type="text" value={inputValue} onChange={changeEventHandler} placeholder="—Å–ø—Ä–æ—Å–∏—Ç–µ –ø—Ä–æ LTE" />
            <button type="submit" disabled={!inputValue}><FaPaperPlane /></button>
        </form>

    )
}

export default InputBar
